body {
  font-family: "Helvetica", sans-serif;
}

p {
  margin: 0 0 5px;
}

a:link, a:visited {
  color: #ba0820;
  text-decoration: none;
}

a:hover, a:focus, a:active {
  color: #ba0820;
  text-decoration: underline;
}

blockquote {
  font-style: italic;
  border-left: solid 3px #7a7a7a;
  margin-left: 20px;
  padding-left: 20px;
}

img {
  max-width: 100%;
  height: auto;
}

hr {
  border-style: solid;
  border-width: 1px 0 0;
  color: currentColor;
}

code:before {
  content: '`';
}

code:after {
  content: '`';
}

pre code:after, pre code:before {
  content: '';
}

.float-left {
  float: left;
}

.float-right {
  float: right;
}

.clear-both {
  clear: both;
}

