.button {
  background-color: #0088ff;
  border-radius: 5px;
  font-weight: 700;
  padding: 15px 60px;
  display: inline-block;
  text-decoration: none;
  text-align: center;
}

.button:link, .button:visited {
  color: white;
  text-decoration: none;
}

.button:hover, .button:focus, .button:active {
  color: white;
  text-decoration: "none";
}

.button--alt {
  background-color: #0088ff;
  border-radius: 5px;
  font-weight: 700;
  padding: 15px 60px;
  display: inline-block;
  text-decoration: none;
  text-align: center;
  background: #999999;
  color: #ba0820;
}

.button--alt:link, .button--alt:visited {
  color: white;
  text-decoration: none;
}

.button--alt:hover, .button--alt:focus, .button--alt:active {
  color: white;
  text-decoration: "none";
}

a.button {
  -moz-appearance: none;
  -webkit-appearance: none;
}

