/**
 * Font application to generic DOM
 */
[class*="icon--"] {
  font-family: "icons";
  display: inline-block;
  vertical-align: middle;
  line-height: 1;
  font-weight: normal;
  font-style: normal;
  speak: none;
  text-decoration: inherit;
  text-transform: none;
  text-rendering: optimizeLegibility;
}

.icon--close::before {
  content: "";
}

.icon--menu::before {
  content: "";
}

.icon--search::before {
  content: "";
}

.breadcrumb {
  margin: 1.25rem 0;
}

.breadcrumb ul, .breadcrumb ol {
  list-style: none;
  margin: 0;
  padding: 0;
}

.breadcrumb ul::after, .breadcrumb ol::after {
  content: "";
  display: table;
  clear: both;
}

.breadcrumb li {
  position: relative;
  float: left;
}

.breadcrumb li:last-child .caret {
  display: none;
}

.breadcrumb a {
  font-size: 1rem;
  padding: 0.75rem;
  display: block;
  line-height: 100%;
  padding-right: 0;
}

.breadcrumb a .caret {
  margin-left: 0.75rem;
  position: relative;
  left: -1px;
}

.breadcrumb.small a {
  font-size: 0.7rem;
  padding: 0.5rem;
  padding-right: 0;
}

.breadcrumb.small a .caret {
  margin-left: 0.5rem;
}

.breadcrumb.large a {
  font-size: 1.3rem;
  padding: 1rem;
  padding-right: 0;
}

.breadcrumb.large a .caret {
  margin-left: 1rem;
}

.caption-block {
  font-size: 1rem;
  margin: 0;
}

.caption-block__body {
