    line-height: 1.5em;
  }
}

.collapsible {
  padding: 1.5em;
  background: #e0e1ef;
}

.collapsible h3 + [aria-hidden] {
  display: none;
}

.collapsible h3 + [aria-hidden='false'] {
  display: block;
}

h3 [aria-expanded] {
  font-family: inherit;
  font-size: inherit;
  display: block;
  width: 100%;
  margin: 0;
  text-align: inherit;
  text-transform: inherit;
}

h3 [aria-expanded]:before {
  content: '\25ba\0020';
}

h3 [aria-expanded='true']:before {
  content: '\25bc\0020';
}

.page {
  display: block;
}

@media (min-width: 1100px) {
  .page--has-sidebar {
    display: flex;
    justify-content: space-between;
  }
  .page--has-sidebar .page__content {
    width: calc(75% - 10px);
  }
  .page--has-sidebar .page__sidebar {
    width: calc(25% - 10px);
  }
}

.site {
  margin-left: auto;
  margin-right: auto;
  padding-left: 5px;
  padding-right: 5px;
}

@media (min-width: 1310px) {
